  margin-bottom: .5em;
  display: block;
}

.webform-field-label:hover {
  cursor: pointer;
}

.webform-checkbox-group {
  padding-top: 15px;
  padding-bottom: 30px;
  border-bottom: 2px solid #9b9b9b;
  lost-flex-container: row;
}

.webform-checkbox-group:focus, .webform-checkbox-group:hover {
  border-bottom-color: #424149;
}

.webform-checkbox-label {
  position: relative;
  margin-bottom: 1rem;
  lost-column: 1/2 flex;
}

.webform-checkbox-label:hover {
  cursor: pointer;
}

.webform-checkbox-label:hover .webform-checkbox-element {
  border-color: #686868;
}

.webform-checkbox {
  position: absolute;
  visibility: hidden;
  width: 0;
  height: 0;
}

.webform-checkbox:checked ~ .webform-checkbox-element:before {
  background-color: #424149;
}

.webform-checkbox-element {
  position: relative;
  display: inline-block;
  width: 16px;
  height: 16px;
  margin-right: .25rem;
  vertical-align: -2px;
  border: 1px solid #8e8e8e;
  border-radius: 2px;
}

.webform-checkbox-element:before {
  position: absolute;
  top: 1px;
  left: 1px;
  display: block;
  width: 12px;
  height: 12px;
  content: '';
  border-radius: 2px;
  background-color: transparent;
}

