  border-color: #5a5964;
}

.skill-pill--is-trainee {
  color: #ababab;
}

.skill-pill--has-link:hover {
  color: #fff;
  border-color: #fff;
}

.skill-pill--has-description {
  background-color: #3b3a41;
}

.skill-pill--is-expert {
  position: relative;
}

.skill-pill--is-expert .skill-pill__title::before {
  position: absolute;
  top: -7px;
  left: 50%;
  width: 1px;
  height: 1px;
  content: '';
  background-color: #9b9b9b;
  display: none;
}

.svg-scroll {
  position: absolute;
  z-index: 2;
  bottom: -140px;
  left: 50%;
  box-sizing: border-box;
  width: 24px;
  height: 36px;
  margin-left: -12px;
  cursor: pointer;
  transition: opacity .5s;
  opacity: 0;
}

@media (max-width: 768px) {
  .svg-scroll {
    display: none !important;
  }
}

.js-svg-scroll--visible {
  opacity: 1;
}

.svg-scroll__mouse {
  width: 25px;
  transition: opacity .5s;
  vertical-align: middle;
  opacity: 1;
}

.svg-scroll__edge {
  stroke: #9b9b9b;
}

.svg-scroll__wheel {
  animation: updown 1s infinite;
  fill: #9b9b9b;
}

@keyframes updown {
  0% {
    transform: translate(0, 0);
  }
  50% {
    transform: translate(0, 5px);
  }
  100% {
    transform: translate(0, 0);
  }
}

